.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.sobra {
  background-color: rgba(0, 0, 0, 0.5);
}

.btn-verde {
  display: inline-block;
  background-color: #1C5025;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
}
.btn-verde:hover {
  background-color: #153d1c;
}

.btn-grisOscuro {
  display: inline-block;
  background-color: #333333;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
}
.btn-grisOscuro:hover {
  background-color: #262626;
}

.btn-amarillo {
  display: inline-block;
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
}
.btn-amarillo:hover {
  background-color: #c77808;
}

.btn-rojo {
  display: inline-block;
  background-color: #ac1313;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
}
.btn-rojo:hover {
  background-color: #951010;
}

.boton {
  border-radius: 0.5rem;
  border: none;
}

.no-webp .inicio {
  background-image: url(../img/jardin1.jpg);
}

.webp .inicio {
  background-image: url(../img/jardin1.webp);
}

.header {
  background-color: #333333;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  color: #FFFFFF;
  text-align: left;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
}
.barra .logo-header {
  max-width: 100%;
  width: 14rem;
  height: auto;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.no-inicio .barra {
  padding-bottom: 3rem;
}

@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
  }
}
.navegacion a {
  text-decoration: none;
  font-size: 2.5rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 1.5rem;
    font-size: 1.8rem;
  }
  .navegacion a.boton {
    margin-top: 0;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
@media (min-width: 1024px) {
  .navegacion a {
    font-size: 2.5rem;
    margin-right: 2rem;
  }
}

.footer {
  background-color: #333333;
  padding: 2.5rem 0;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer .copyright {
  text-align: center;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
  font-weight: 400;
}

@media (min-width: 768px) {
  .contenedor-sistemas {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.sistema {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
  margin-bottom: 3rem;
}
.sistema .contenido-sistema {
  padding: 2rem;
}
.sistema h3, .sistema p {
  margin: 0;
}
.sistema .precio {
  color: #419e68;
  font-weight: 700;
  font-size: 3rem;
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
}
.iconos-caracteristicas li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.iconos-caracteristicas li p {
  font-weight: 900;
}

.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario legend p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}
.formulario input[type=submit] {
  border: none;
}
.formulario input[type=submit]:hover {
  cursor: pointer;
}

.webp .iniciarSesion_img {
  background-image: url(../img/jardin2.webp);
}

.no-webp .iniciarSesion_img {
  background-image: url(../img/jardin2.png);
}

.iniciarSesion_img {
  background-position: center center;
  background-size: cover;
  height: 25rem;
}
@media (min-width: 768px) {
  .iniciarSesion_img {
    height: 40rem;
  }
}

.registrarse {
  font-size: 2rem;
}
.registrarse a {
  color: #000000;
}
.registrarse span {
  color: #1C5025;
}
.registrarse::after {
  content: "";
  display: block;
  width: 24rem;
  height: 0.5rem;
  background-color: #1C5025;
  margin-top: 0.5rem;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .registrarse::after {
    width: 40rem;
  }
}

.webp .registrate_img {
  background-image: url(../img/jardin1.webp);
}

.no-webp .registrate_img {
  background-image: url(../img/jardin1.png);
}

.registrate_img {
  background-position: center center;
  background-size: cover;
  height: 25rem;
}
@media (min-width: 768px) {
  .registrate_img {
    height: 40rem;
  }
}

.webp .imagen-nosotros {
  background-image: url(../img/jardin1.webp);
}

.no-webp .imagen-nosotros {
  background-image: url(../img/jardin1.png);
}

.imagen-nosotros {
  background-position: center center;
  background-size: cover;
  width: 100%;
  height: 30rem;
}
@media (min-width: 768px) {
  .imagen-nosotros {
    height: 50rem;
    margin: auto 0;
  }
}

.contenedor-nosotros {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .contenedor-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.webp .inicio_img {
  background-image: url(../img/jardin1.webp);
}

.no-webp .inicio_img {
  background-image: url(../img/jardin1.png);
}

.inicio_img {
  background-position: center center;
  background-size: cover;
  height: 25rem;
}
@media (min-width: 768px) {
  .inicio_img {
    height: 40rem;
  }
}

.anuncio {
  display: block;
  padding: 1rem;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 700;
  background-color: #E08709;
  border-radius: 0.5rem;
  margin-bottom: 5rem;
}
.anuncio::after {
  content: "";
  display: block;
  width: 30rem;
  height: 0.3rem;
  background-color: #1C5025;
  margin-top: 0.5rem;
  border-radius: 0.5rem;
}

.dispositivo {
  display: flex;
  flex-direction: column;
  border: 0.5rem solid #333333;
  border-radius: 0.2rem;
  padding: 0.8rem;
  margin-bottom: 2rem;
}

.contenedor-info-botones {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .contenedor-info-botones {
    padding: 0 0.5rem;
  }
}
@media (min-width: 1024px) {
  .contenedor-info-botones {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(2, 1fr);
  }
}

.dispositivo-info {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.dispositivo-info p {
  margin: 0;
}
@media (min-width: 768px) {
  .dispositivo-info {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
@media (min-width: 1024px) {
  .dispositivo-info {
    justify-content: space-around;
  }
}

.dispositivo-botones {
  display: flex;
  flex-direction: column;
}
.dispositivo-botones .boton {
  margin-top: 0.2rem 0;
}
.dispositivo-botones .boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .dispositivo-botones {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .dispositivo-botones .boton {
    margin: 0;
  }
}
@media (min-width: 1024px) {
  .dispositivo-botones {
    justify-content: space-around;
  }
}

.boton-encendido {
  width: 5rem;
  height: 5rem;
  margin: 0.2rem auto;
}
@media (min-width: 768px) {
  .boton-encendido {
    margin: 0;
  }
}
.boton-encendido:hover {
  cursor: pointer;
}

.contenedor-descripcion {
  border-top: 0.5rem solid #333333;
  border-radius: 0.2rem;
}

.alerta {
  color: #FFFFFF;
  text-align: center;
  display: block;
  margin: 1rem auto;
}
.alerta.exito {
  background-color: #1C5025;
}
.alerta.error {
  background-color: #ac1313;
}